
import React from 'react';
import Button from '@mui/material/Button';
import styled from './loader.module.scss';

export const Loader = ({ setLoader }) => {
    React.useEffect(() => {
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('bg-img')
    }, [])
    const onGo = () => {
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('bg-img')
        setLoader(false);
    }
    return <Button onClick={onGo} className={styled.button} variant="contained">Let's Go</Button>
    
    
}